import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { MDBContainer, MDBRow, MDBCol, MDBInput, MDBBtn } from "mdbreact";
import Step from "./Step";

export default function Step1(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();

  const validate = () => {
    setTouched(true);
    const isValid = formRef.current.checkValidity();
    setErrorMsg(!isValid ? "Please fill out all fields" : "");
  };
  useEffect(() => {
    setTouched(formRef.current.checkValidity());
  }, []);

  const states = [
    "Alabama",
    "Alaska",
    "American Samoa",
    "Arizona",
    "Arkansas",
    "California",
    "Colorado",
    "Connecticut",
    "Delaware",
    "District of Columbia",
    "Federated States of Micronesia",
    "Florida",
    "Georgia",
    "Guam",
    "Hawaii",
    "Idaho",
    "Illinois",
    "Indiana",
    "Iowa",
    "Kansas",
    "Kentucky",
    "Louisiana",
    "Maine",
    "Marshall Islands",
    "Maryland",
    "Massachusetts",
    "Michigan",
    "Minnesota",
    "Mississippi",
    "Missouri",
    "Montana",
    "Nebraska",
    "Nevada",
    "New Hampshire",
    "New Jersey",
    "New Mexico",
    "New York",
    "North Carolina",
    "North Dakota",
    "Northern Mariana Islands",
    "Ohio",
    "Oklahoma",
    "Oregon",
    "Palau",
    "Pennsylvania",
    "Puerto Rico",
    "Rhode Island",
    "South Carolina",
    "South Dakota",
    "Tennessee",
    "Texas",
    "Utah",
    "Vermont",
    "Virgin Island",
    "Virginia",
    "Washington",
    "West Virginia",
    "Wisconsin",
    "Wyoming",
  ];

  return (
    <Step
      heading={
        "What's your Mailing address?"
      }
    >
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={10} md={8}>
              <MDBRow>
                <MDBCol md={6}>
                  <MDBInput
                    color="pink"
                    label="suite/appartment no."
                    group
                    type="text"
                    name="mailingaptno"
                    value={state.mailingaptno}
                    onChange={(e) => {
                      validate();
                      onChangeField(e, e.target.name, e.target.value);
                    }}
                    required
                    containerClass="mb-0"
                  />
                </MDBCol>
                <MDBCol md={6}>
                  <MDBInput
                    label="Street"
                    group
                    type="text"
                    name="mailingStreet"
                    value={state.mailingStreet}
                    onChange={(e) => {
                      validate();
                      onChangeField(e, e.target.name, e.target.value);
                    }}
                    required
                    containerClass="mb-0"
                  />
                </MDBCol>
                <MDBCol md={6}>
                  <MDBInput
                    label="city"
                    group
                    type="text"
                    name="mailingcity"
                    pattern="^[a-zA-Z ][a-zA-Z\\s]+$"
                    value={state.mailingcity}
                    onChange={(e) => {
                      validate();
                      onChangeField(e, e.target.name, e.target.value);
                    }}
                    required
                    containerClass="mb-0"
                  />
                </MDBCol>
                <MDBCol>
                  <MDBInput
                    label="Zipcode"
                    group
                    type="number"
                    name="mailingZipCode"
                    pattern="^[0-9]+$"
                    value={state.mailingZipCode}
                    onChange={(e) => {
                      validate();
                      onChangeField(e, e.target.name, e.target.value);
                    }}
                    required
                    containerClass="mb-0"
                  />
                </MDBCol>
                <MDBCol md={6}>
                  <MDBCol className="mt-3 mb-3 mr-0">
                    <select
                      className="browser-default custom-select"
                      name="mailingState"
                      value={state.mailingState}
                      onChange={(e) => {
                        validate();
                        onChangeField(e, e.target.name, e.target.value);
                      }}
                      required
                    >
                      <option selected disabled>
                        Choose your option
                      </option>
                      {states.map((state) => (
                        <option value={state}>{state}</option>
                      ))}
                    </select>
                  </MDBCol>
                </MDBCol>
              </MDBRow>

              <MDBRow center className="d-flex flex-column align-items-center">
                {touched && errorMsg.length > 0 && (
                  <div className="text-danger mb-2">{errorMsg}</div>
                )}
                {touched && !errorMsg && (
                  <Link to={nextStep}>
                    <MDBBtn
                      color="pink"
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Next
                    </MDBBtn>
                  </Link>
                )}
                {(!touched || errorMsg) && (
                  <MDBBtn
                    color={"grey"}
                    disabled
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Next
                  </MDBBtn>
                )}
              </MDBRow>
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}
