import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { MDBContainer, MDBRow, MDBCol, MDBInput, MDBBtn } from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";

export default function Step4(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();
  const validate = () => {
    setTouched(true);
  
   if(formRef.current!==null) {
    const isValid = formRef.current.checkValidity();
    setErrorMsg(!isValid ? "Please fill out all fields" : "");}
  };

  return (
    <Step heading={`Have you ever been widowed or divorced?`}>
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <MDBRow center>
          <MDBCol sm={10} md={8}>
            <div className="my-4" />
            <MDBRow center>
              <MDBCol lg={3}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "divorced", true);
                  }}
                >
                  Yes
                </MDBBtn>
              </MDBCol>
              <MDBCol className="d-block d-lg-none my-2" />
              <MDBCol lg={3}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "divorced", false);
                    history.push(nextStep);
                  }}
                >
                  No
                </MDBBtn>
              </MDBCol>
            </MDBRow>

            {state.divorced && (
              <form ref={formRef} onSubmit={(e)=>e.preventDefault()}>
                <MDBRow center className="mt-5">
                  <MDBCol lg={6}>
                    <h4>What date were you widowed or divorced</h4>
                  </MDBCol>
                </MDBRow>
                <MDBRow center>
                  <MDBCol lg={6}>
                    <MDBInput
                      label="Date of Birth"
                      group
                      type="date"
                      name="dateDivorcedORWidowed"
                      value={state.dateDivorcedORWidowed}
                      onChange={(e) => {
                        onChangeField(e, e.target.name, e.target.value);
                        validate();
                      }}
                      required
                      containerClass="mb-0"
                    />
                  </MDBCol>
                </MDBRow>
                <MDBRow
                  center
                  className="d-flex flex-column align-items-center"
                >
                  {touched && errorMsg.length > 0 && (
                    <div className="text-danger mb-2">{errorMsg}</div>
                  )}
                  {touched && !errorMsg && (
                    <Link to={nextStep}>
                      <MDBBtn
                        color="pink"
                        rounded
                        type="button"
                        className="z-depth-1a"
                      >
                        Next
                      </MDBBtn>
                    </Link>
                  )}
                  {(!touched || errorMsg) && (
                    <MDBBtn
                      color={"grey"}
                      disabled
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Next
                    </MDBBtn>
                  )}
                </MDBRow>
              </form>
            )}
          </MDBCol>
        </MDBRow>
      </MDBContainer>
    </Step>
  );
}
