// Fantastic (NAME), We have everything we need to obtain your EIN Please complete your payment details below alongside agree to our terms of service and you will receive your EIN via email within the next 24-48 hours.

import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBCard,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";

export default function Step9(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();

  const validate = () => {
    setTouched(true);
    const isValid = formRef.current.checkValidity();
    setErrorMsg(!isValid ? "Please enter valid details" : "");
    if (state.creditCardNumber.length < 8) {
      setErrorMsg("Please enter valid credit card number");
    }

    if (state.CVV.length < 3) {
      setErrorMsg("Please enter valid CVV");
    }
  };

  useEffect(() => {
    setTouched(formRef.current.checkValidity());
  }, []);
  return (
    <Step
      heading={`Fantastic! That's everything I just need to collect your payment information and have you agree to our terms of service`}
    >
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={10} md={4}>
              <MDBRow md={12}>
                <MDBCol>
                  <MDBInput
                    label="Credit Card Number"
                    value={state.creditCardNumber}
                    min="10000000"
                    onChange={(e) => {
                      validate();
                      onChangeField(e, "creditCardNumber", e.target.value);
                    }}
                    outline
                    icon="credit-card"
                    required
                  />
                </MDBCol>
              </MDBRow>
              <MDBRow md={12}>
                <MDBCol>
                  <MDBInput
                    label="EXP"
                    value={state.EXP}
                    onChange={(e) => onChangeField(e, "EXP", e.target.value)}
                    type="date"
                    outline
                    icon="date"
                    required
                  />
                </MDBCol>
                <MDBCol>
                  <MDBInput
                    label="CVV"
                    type="number"
                    maxLength="3"
                    outline
                    value={state.CVV}
                    required
                    onChange={(event) => {
                      validate();
                      var { value, maxLength } = event.target;
                      value = value.slice(0, maxLength);
                      onChangeField(event, "CVV", value);
                    }}
                    icon="lock"
                  />
                </MDBCol>
              </MDBRow>

              <MDBRow center className="d-flex flex-column align-items-center">
                {touched && errorMsg.length > 0 && (
                  <div className="text-danger mb-2">{errorMsg}</div>
                )}
                {touched && !errorMsg && (
                  <Link to={nextStep}>
                    <MDBBtn
                      color="pink"
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Submit
                    </MDBBtn>
                  </Link>
                )}
                {(!touched || errorMsg) && (
                  <MDBBtn
                    color={"grey"}
                    disabled
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Submit
                  </MDBBtn>
                )}
              </MDBRow>
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}
