import React, { useState } from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBCard,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";

export default function Step20(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();
  const validate = () => {
    setTouched(true);
    const isValid = formRef.current.checkValidity();

    setErrorMsg(!isValid ? "Please enter valid phone number" : "");
  };

  return (
    <Step
      heading={`It's the last question! Have you ever applied for or been issued a US Passport?`}
    >
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <MDBRow center>
          <MDBCol sm={10} md={8}>
            <div className="my-4" />
            <MDBRow center>
              <MDBCol md={6} lg={4}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "usPassport", true);
                  }}
                >
                  Yes
                </MDBBtn>
              </MDBCol>
              <MDBCol className="d-block d-lg-none my-2"></MDBCol>
              <MDBCol md={6} lg={4}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "usPassport", false);
                    history.push(nextStep);
                  }}
                >
                  No
                </MDBBtn>
              </MDBCol>
            </MDBRow>
            {state.usPassport && (
              <MDBRow center className="mt-5 mb-5">
                <MDBCol lg={12} className="justify-self-center">
                  <h4 className="d-flex justify-content-center">
                    Which of these do you have
                  </h4>
                </MDBCol>
                <MDBCol md={6} lg={4}>
                  <MDBBtn
                    color="pink"
                    rounded
                    type="button"
                    className="btn-block z-depth-1a"
                    onClick={(e) => {
                      onChangeField(e, "PassportBookOrCard", "Book");
                    }}
                  >
                    Book
                  </MDBBtn>
                </MDBCol>
                <MDBCol className="d-block d-lg-none my-2"></MDBCol>
                <MDBCol md={6} lg={4}>
                  <MDBBtn
                    color="pink"
                    rounded
                    type="button"
                    className="btn-block z-depth-1a"
                    onClick={(e) => {
                      onChangeField(e, "PassportBookOrCard", "Card");
                    }}
                  >
                    card
                  </MDBBtn>
                </MDBCol>
                <MDBCol className="d-block d-lg-none my-2"></MDBCol>
                <MDBCol md={6} lg={4}>
                  <MDBBtn
                    color="pink"
                    rounded
                    type="button"
                    className="btn-block z-depth-1a"
                    onClick={(e) => {
                      onChangeField(e, "PassportBookOrCard", "Both");
                    }}
                  >
                    Both
                  </MDBBtn>
                </MDBCol>
              </MDBRow>
            )}
            {(state.PassportBookOrCard === "Both" || state.PassportBookOrCard === "Book") && (
              <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
                <MDBCard className="mt-3">
                  <div className="p-4">
                    <MDBRow center>
                      <MDBCol>
                        <h5>IF you have passport book fill this</h5>
                        <MDBInput
                          label="Name on passport"
                          group
                          type="text"
                          name="nameOnPassportBook"
                          value={state.nameOnPassportBook}
                          onChange={(e) => {
                            onChangeField(e, e.target.name, e.target.value);
                            validate();
                          }}
                          required
                          containerClass="mb-0"
                        />
                      </MDBCol>
                    </MDBRow>
                    <MDBRow center>
                      <MDBCol>
                        <MDBInput
                          label="Passport Book Number"
                          group
                          type="number"
                          name="passportBookNumber"
                          value={state.passportBookNumber}
                          onChange={(e) => {
                            onChangeField(e, e.target.name, e.target.value);
                            validate();
                          }}
                          required
                          containerClass="mb-0"
                        />
                      </MDBCol>
                    </MDBRow>
                    <MDBRow center>
                      <MDBCol>
                        <MDBInput
                          label="Passport Book isse date"
                          group
                          type="date"
                          name="passportBookIssueDate"
                          value={state.passportBookIssueDate}
                          onChange={(e) => {
                            onChangeField(e, e.target.name, e.target.value);
                            validate();
                          }}
                          required
                          containerClass="mb-0"
                        />
                      </MDBCol>
                    </MDBRow>

                    <MDBCol>
                      <div class="form-check">
                        <input
                          type="radio"
                          class="form-check-input"
                          name="book"
                          onClick={() => {
                            onChangeField(
                              "e",
                              "passportBookStatus",
                              "submitting"
                            );
                          }}
                          checked={
                            state.passportBookStatus === "submitting"
                              ? true
                              : false
                          }
                        />
                        <label class="form-check-label" for="materialUnchecked">
                          Submitting with application{" "}
                        </label>
                      </div>
                    </MDBCol>
                    <MDBCol>
                      <div class="form-check">
                        <input
                          type="radio"
                          class="form-check-input"
                          name="book"
                          onClick={() => {
                            onChangeField("e", "passportBookStatus", "Stolen");
                          }}
                          checked={
                            state.passportBookStatus === "Stolen" ? true : false
                          }
                        />
                        <label
                          class="form-check-label"
                          for="materialUnchecked"
                          for="defaultChecked2"
                        >
                          Stolen
                        </label>
                      </div>
                    </MDBCol>
                    <MDBCol>
                      <div class="form-check">
                        <input
                          type="radio"
                          class="form-check-input"
                          name="book"
                          onClick={() => {
                            onChangeField("e", "passportBookStatus", "lost");
                          }}
                          checked={
                            state.passportBookStatus === "lost" ? true : false
                          }
                        />
                        <label
                          class="form-check-label"
                          for="materialUnchecked"
                          for="defaultChecked2"
                        >
                          Lost
                        </label>
                      </div>
                    </MDBCol>
                    <MDBCol>
                      <div class="form-check">
                        <input
                          type="radio"
                          class="form-check-input"
                          name="book"
                          onClick={() => {
                            onChangeField(
                              "e",
                              "passportBookStatus",
                              "inpossession"
                            );
                          }}
                          checked={
                            state.passportBookStatus === "inpossession"
                              ? true
                              : false
                          }
                        />
                        <label
                          class="form-check-label"
                          for="materialUnchecked"
                          for="defaultChecked2"
                        >
                          In my possession
                        </label>
                      </div>
                    </MDBCol>
                  </div>
                </MDBCard>
              </form>
            )}

            {(state.PassportBookOrCard === "Both" ||
              state.PassportBookOrCard === "Card") && (
              <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
                <MDBCard className="mt-3">
                  <div className="p-4">
                    <MDBRow center>
                      <MDBCol>
                        <h5>IF you have passport Card fill this</h5>
                        <MDBInput
                          label="Name on passport"
                          group
                          type="text"
                          name="nameOnPassportCard"
                          value={state.nameOnPassportCard}
                          onChange={(e) => {
                            onChangeField(e, e.target.name, e.target.value);
                            validate();
                          }}
                          required
                          containerClass="mb-0"
                        />
                      </MDBCol>
                    </MDBRow>
                    <MDBRow center>
                      <MDBCol>
                        <MDBInput
                          label="Passport Card Number"
                          group
                          type="number"
                          name="passportCardNumber"
                          value={state.passportCardNumber}
                          onChange={(e) => {
                            onChangeField(e, e.target.name, e.target.value);
                            validate();
                          }}
                          required
                          containerClass="mb-0"
                        />
                      </MDBCol>
                    </MDBRow>
                    <MDBRow center>
                      <MDBCol>
                        <MDBInput
                          label="Passport Book issue date"
                          group
                          type="date"
                          name="passportCardIssueDate"
                          value={state.passportCardIssueDate}
                          onChange={(e) => {
                            onChangeField(e, e.target.name, e.target.value);
                            validate();
                          }}
                          required
                          containerClass="mb-0"
                        />
                      </MDBCol>
                    </MDBRow>

                    <MDBCol>
                      <div class="form-check">
                        <input
                          type="radio"
                          class="form-check-input"
                          name="card"
                          onClick={() => {
                            onChangeField(
                              "e",
                              "passportCardStatus",
                              "submitting"
                            );
                          }}
                          checked={
                            state.passportCardStatus === "submitting"
                              ? true
                              : false
                          }
                        />
                        <label class="form-check-label" for="materialUnchecked">
                          Submitting with application{" "}
                        </label>
                      </div>
                    </MDBCol>
                    <MDBCol>
                      <div class="form-check">
                        <input
                          type="radio"
                          class="form-check-input"
                          name="card"
                          onClick={() => {
                            onChangeField("e", "passportCardStatus", "Stolen");
                          }}
                          checked={
                            state.passportCardStatus === "Stolen" ? true : false
                          }
                        />
                        <label
                          class="form-check-label"
                          for="materialUnchecked"
                          for="defaultChecked2"
                        >
                          Stolen
                        </label>
                      </div>
                    </MDBCol>
                    <MDBCol>
                      <div class="form-check">
                        <input
                          type="radio"
                          class="form-check-input"
                          name="card"
                          onClick={() => {
                            onChangeField("e", "passportCardStatus", "lost");
                          }}
                          checked={
                            state.passportCardStatus === "lost" ? true : false
                          }
                        />
                        <label
                          class="form-check-label"
                          for="materialUnchecked"
                          for="defaultChecked2"
                        >
                          Lost
                        </label>
                      </div>
                    </MDBCol>
                    <MDBCol>
                      <div class="form-check">
                        <input
                          type="radio"
                          class="form-check-input"
                          name="card"
                          onClick={() => {
                            onChangeField(
                              "e",
                              "passportCardStatus",
                              "inpossession"
                            );
                          }}
                          checked={
                            state.passportCardStatus === "inpossession"
                              ? true
                              : false
                          }
                        />
                        <label
                          class="form-check-label"
                          for="materialUnchecked"
                          for="defaultChecked2"
                        >
                          In my possession
                        </label>
                      </div>
                    </MDBCol>
                  </div>
                </MDBCard>
              </form>
            )}
          </MDBCol>
        </MDBRow>
        {state.usPassport && (<MDBRow center>
          <Link to={nextStep}>
            <MDBBtn color="pink" rounded type="button" className="z-depth-1a">
              Next
            </MDBBtn>
          </Link>
        </MDBRow>)}
      </MDBContainer>
    </Step>
  );
}
