import React, { Component } from "react";
import { Route, Switch, Redirect } from "react-router-dom";
import axios from "axios";
import Step1 from "./Step1";
import Step2 from "./Step2";
import Step3 from "./Step3";
import Step4 from "./Step4";
import Step5 from "./Step5";
import Step6 from "./Step6";
import Step7 from "./Step7";
import Step8 from "./Step8";
import Step9 from "./Step9";
import Step10 from "./Step10";
import Step11 from "./Step11";
import Step12 from "./Step12";
import Step13 from "./Step13";
import Step14 from "./Step14";
import Step15 from "./Step15";
import Step16 from "./Step16";
import Step17 from "./Step17";
import Step18 from "./Step18";
import Step19 from "./Step19";
import Step19a from "./Step19.1";
import Step20 from "./Step20";
import Step21 from "./Step21";
import Step22 from "./Step22";
import Step23 from "./Step23";
import Step24 from "./Step24";
import Step25 from "./Step25";
import Step26 from "./Step26";
// import AllOptions from "./AllOptions";
import ThankYou from "./ThankYou";
import Decline from "./Decline";
export default class MultiStepForm extends Component {
                 state = {
                   // step1
                   firstName: "",
                   lastName: "",


                   //step5
                   latLng: {},
                   streetAddress: "",
                   aptUnitNo: "",
                   city: "",
                   state: "",
                   zipCode: "",

                   // step4
                   sex: "",

                   // step2
                   type: "",

                   // step6
                   franchiseOfCompany: "",

                   // step7
                   growsRevenueFor12Months: "",

                   // step8
                   phone: "",

                   // step6
                   ssn: "",

                   // step7
                   email: "",

                   // step9
                   mailingAddress: "",
                   mailingcity: "",
                   mailingStreet: "",
                   mailingaptno: "",
                   mailingZipCode: "",
                   mailingState: "",
                   // step10
                   maidennames: "",

                   // step11
                   parent1firstName: "",
                   parent1lastName: "",
                   parent1DoB: "",
                   parent1PlaceOfBirth: "",
                   parent1Sex: "",
                   parent1CitizenShip: "",

                   // step12
                   parent2firstName: "",
                   parent2lastName: "",
                   parent2DoB: "",
                   parent2PlaceOfBirth: "",
                   parent2Sex: "",
                   parent2CitizenShip: "",

                   // step12 
                   married: "",

                   // step13
                   spouseFirstName: "",
                   spouseLastName: "",

                   // step14:
                   spouseDOB: "",

                   // step15
                   spouseaptUnitNo: "",
                   spouselatLng: {},
                   spousestreetAddress: "",
                   spouseaptUnitNo: "",
                   spousecity: "",
                   spousestate: "",
                   spousezipCode: "",

                   // step16
                   spouseCitizenShip: "",

                   // step17
                   dateMarried: "",

                   //step18
                   divorced: "",
                   dateDivorcedORWidowed: "",

                   // step3
                   dob: "",

                   // step19
                   additionalphone: "",

                   // step19a

                   //step 19a
                   Occupation: "",
                   employerOrSchool: "",
                   
                   height: 0,
                   weight: 0,
                   feet: "",
                   eyeColor: "",
                   hairColor: "",

                   // step21
                   emergencyFullName: "",
                   emergencyAddress: "",
                   emergencyPhone: "",
                   emergencyRelationship: "",

                   // step22
                   usPassport: "",

                   PassportBookOrCard: "",

                   passportBookNumber: "",
                   nameOnPassportBook: "",
                   passportBookIssueDate: "",
                   passportBookStatus: "",

                   passportCardNumber: "",
                   nameOnPassportCard: "",
                   passportCardIssueDate: "",
                   passportCardStatus: "",
                   submitting: false,
                   // step23
                   creditCardNumber: "",
                   CVV: "",
                   EXP:"",

                   // step24
                  

                   // step25
                   agree: "",
                 };
                 handleChangeField = (e, name, value) => {
                   this.setState(
                     {
                       [name]: value,
                     },
                     () => console.log(this.state)
                   );
                 };

                 submitData() {
                   const data = {
                     firstName: this.state.firstName,
                     lastName: this.state.lastName,

                     typePassport: this.state.type,
                     dob: this.state.dob,
                     sex: this.state.sex,

                     bornPlace: `${this.city} ${this.state}`,
                     ssn: this.state.ssn,
                     email: this.state.email,

                     phone: this.state.phone,

                     mailingAddress: `${this.mailingaptno} ${this.mailingStreet} ${this.mailingState} ${this.mailingZipCode}`,

                     maindenNames: this.state.maidennames,
                     parent1: {
                       firstName: this.state.parent1firstName,
                       lastName: this.state.parent1lastName,
                       DOB: this.state.parent1DoB,
                       placeOfBirth: this.state.parent1PlaceOfBirth,
                       sex: this.state.parent1Sex,
                       usCitizen: this.state.parent2CitizenShip,
                     },

                     parent2: {
                       firstName: this.state.parent2firstName,
                       lastName: this.state.parent2lastName,
                       DOB: this.state.parent2DoB,
                       placeOfBirth: this.state.parent2PlaceOfBirth,
                       sex: this.state.parent2Sex,
                       usCitizen: this.state.parent2CitizenShip,
                     },

                     marriage: {
                       currentSpouseFirstName: this.state.spouseFirstName,
                       currentSpouseLastName: this.state.spouseLastName,
                       currentSpouseDOB: this.state.spouseDOB,
                       currentSpousePlaceOFBirth: this.state.spousecity,
                       marriageDate: this.state.dateMarried,
                       widowedOrDivorced: this.state.widowedOrDivorced,
                       widowedDate: this.state.dateDivorcedORWidowed,
                     },

                     additionalContactNumber: this.state.additionalphone,
                     occupation: this.state.Occupation,
                     EmployerOrSchool: this.state.EmployerOrSchool,

                     moreDetials: {
                       height: this.state.height,
                       hairColor: this.state.hairColor,
                       eyeColor: this.state.eyeColor,
                     },

                     emergencyContact: {
                       fullName: this.state.emergencyFullName,
                       phone: this.state.emergencyPhone,
                       address: this.state.emergencyAddress,
                       relationship: this.state.emergencyAddress,
                     },

                     passportDetails: {
                       Book: {
                         name: this.state.nameOnPassportBook,
                         number: this.state.passportBookNumber,
                         status: this.state.passportBookStatus,
                         issueDate: this.state.passportBookIssueDate,
                       },

                       Card: {
                         name: this.state.nameOnPassportCard,
                         number: this.state.passportCardNumber,
                         status: this.state.passportCardStatus,
                         issueDate: this.state.passportCardIssueDate,
                       },
                     },

                     creditCardNumber: this.state.creditCardNumber,
                     CVV: this.state.CVV,

                     state: this.state.state,
                     address: `${this.state.step19_streetAddress}, ${this.state.step19_city}, ${this.state.step19_state} ${this.state.step19_zipCode}`,
                     usCitizen: this.state.usCitizen,

                     agree: this.state.agree,
                   };

                   alert(JSON.stringify(data))
                  //  axios
                  //    .post(
                  //      "https://hooks.zapier.com/hooks/catch/1712527/o5y1jta/",
                  //      data
                  //    )
                  //    .then((response) => console.log(response))
                  //    .finally(() => console.log("Data Sent", { data: data }));
                 }

                 renderStep = (props) => {
                   const { history } = props;
                   const { path, params } = props.match;
                   if (params.step === "thankyou") {
                     this.submitData();
                     return <ThankYou />;
                   } else if (params.step === "decline") {
                     return <Decline />;
                   }
                   const step = parseInt(params.step);
                   const currentPath = `/${path.split("/")[1]}`;
                   switch (step) {
                     case 1:
                       return (
                         <Step1
                           history={history}
                           nextStep={`${currentPath}/2`}
                           state={this.state}
                           onChangeField={this.handleChangeField}
                         />
                       );
                     case 2:
                       return (
                         <Step2
                           history={history}
                           prevStep={`${currentPath}/1`}
                           nextStep={`${currentPath}/3`}
                           state={this.state}
                           onChangeField={this.handleChangeField}
                         />
                       );
                     case 3:
                       console.log("step 3");

                       return (
                         <Step3
                           history={history}
                           prevStep={`${currentPath}/2`}
                           nextStep={`${currentPath}/4`}
                           state={this.state}
                           onChangeField={this.handleChangeField}
                         />
                       );
                     case 4:
                       console.log("step 4");
                       return (
                         <Step4
                           history={history}
                           prevStep={`${currentPath}/3`}
                           nextStep={`${currentPath}/5`}
                           state={this.state}
                           onChangeField={this.handleChangeField}
                         />
                       );
                     case 5:
                       return (
                         <Step5
                           history={history}
                           prevStep={`${currentPath}/4`}
                           nextStep={`${currentPath}/6`}
                           state={this.state}
                           onChangeField={this.handleChangeField}
                         />
                       );
                     case 6:
                       return (
                         <Step6
                           history={history}
                           prevStep={`${currentPath}/5`}
                           nextStep={`${currentPath}/7`}
                           state={this.state}
                           onChangeField={this.handleChangeField}
                         />
                       );
                     case 7:
                       return (
                         <Step7
                           history={history}
                           prevStep={`${currentPath}/6`}
                           nextStep={`${currentPath}/8`}
                           state={this.state}
                           onChangeField={this.handleChangeField}
                         />
                       );
                     case 8:
                       return (
                         <Step8
                           history={history}
                           prevStep={`${currentPath}/7`}
                           nextStep={`${currentPath}/9`}
                           state={this.state}
                           onChangeField={this.handleChangeField}
                         />
                       );
                     case 9:
                       return (
                         <Step9
                           history={history}
                           prevStep={`${currentPath}/8`}
                           nextStep={
                             this.state.sex == "female"
                               ? `${currentPath}/10`
                               : `${currentPath}/11`
                           }
                           state={this.state}
                           onChangeField={this.handleChangeField}
                         />
                       );
                     case 10:
                       return (
                         <Step10
                           history={history}
                           prevStep={`${currentPath}/9`}
                           nextStep={`${currentPath}/11`}
                           state={this.state}
                           onChangeField={this.handleChangeField}
                         />
                       );
                     case 11:
                       return (
                         <Step11
                           history={history}
                           prevStep={`${currentPath}/10`}
                           nextStep={`${currentPath}/12`}
                           state={this.state}
                           onChangeField={this.handleChangeField}
                         />
                       );
                     case 12:
                       return (
                         <Step12
                           history={history}
                           state={this.state}
                           prevStep={`${currentPath}/11`}
                           nextStep={
                            
                                `${currentPath}/13`
                               
                           }
                           onChangeField={this.handleChangeField}
                         />
                       );
                     case 13:
                       return (
                         <Step13
                           history={history}
                           prevStep={`${currentPath}/12`}
                           nextStep={`${currentPath}/14`}
                           state={this.state}
                           onChangeField={this.handleChangeField}
                         />
                       );
                     case 14:
                       return (
                         <Step14
                           history={history}
                           prevStep={`${currentPath}/13`}
                           nextStep={`${currentPath}/15`}
                           state={this.state}
                           onChangeField={this.handleChangeField}
                         />
                       );
                     case 15:
                       return (
                         <Step15
                           history={history}
                           prevStep={`${currentPath}/14`}
                           nextStep={`${currentPath}/16`}
                           state={this.state}
                           onChangeField={this.handleChangeField}
                         />
                       );

                     case 16:
                       return (
                         <Step16
                           history={history}
                           prevStep={`${currentPath}/15`}
                           nextStep={`${currentPath}/17`}
                           state={this.state}
                           onChangeField={this.handleChangeField}
                         />
                       );
                     case 17:
                       return (
                         <Step17
                           history={history}
                           prevStep={`${currentPath}/16`}
                           nextStep={`${currentPath}/18`}
                           state={this.state}
                           onChangeField={this.handleChangeField}
                         />
                       );
                     case 18:
                       return (
                         <Step18
                           history={history}
                           prevStep={`${currentPath}/17`}
                           nextStep={`${currentPath}/19`}
                           state={this.state}
                           onChangeField={this.handleChangeField}
                         />
                       );
                     case 19:
                       return (
                         <Step19
                           history={history}
                           prevStep={
                             this.state.married ?
                             `${currentPath}/18` :
                              `${currentPath}/12`
                            }
                           nextStep={`${currentPath}/20`}
                           state={this.state}
                           onChangeField={this.handleChangeField}
                         />
                       );

                     case 20:
                       return (
                         <Step19a
                           history={history}
                           prevStep={`${currentPath}/19`}
                           nextStep={`${currentPath}/21`}
                           state={this.state}
                           onChangeField={this.handleChangeField}
                         />
                       );
                     case 21:
                       return (
                         <Step20
                           history={history}
                           prevStep={`${currentPath}/20`}
                           nextStep={`${currentPath}/22`}
                           state={this.state}
                           onChangeField={this.handleChangeField}
                         />
                       );
                     case 22:
                       return (
                         <Step21
                           history={history}
                           prevStep={`${currentPath}/21`}
                           nextStep={`${currentPath}/23`}
                           state={this.state}
                           onChangeField={this.handleChangeField}
                         />
                       );
                     case 23:
                       return (
                         <Step22
                           history={history}
                           prevStep={`${currentPath}/22`}
                           nextStep={`${currentPath}/24`}
                           state={this.state}
                           onChangeField={this.handleChangeField}
                         />
                       );
                     case 24:
                       return (
                         <Step23
                           history={history}
                           prevStep={`${currentPath}/23`}
                           nextStep={`${currentPath}/25`}
                           state={this.state}
                           onChangeField={this.handleChangeField}
                         />
                       );
                     case 25:
                       return (
                         <Step24
                           history={history}
                           prevStep={`${currentPath}/23`}
                           nextStep={`${currentPath}/thankyou`}
                           state={this.state}
                           onChangeField={this.handleChangeField}
                         />
                       );
                     default:
                       return <div>invalid step</div>;
                   }
                 };
                 render() {
                   const { path } = this.props.match;
                   return (
                     <Switch>
                       <Route
                         path="/start"
                         exact
                         render={() => <Redirect to={`${path}/1`} />}
                       />
                       <Route
                         path={`${path}/:step`}
                         render={(props) => (
                           <>
                             {this.renderStep(props)}
                             <small
                               style={{
                                 position: "absolute",
                                 width: "100%",
                                 bottom: 0,
                                 textAlign: "center",
                                 padding: 10,
                               }}
                             ></small>
                           </>
                         )}
                       />
                     </Switch>
                   );
                 }
               }
